import React, { Component } from "react";

import { List, NavBar, Icon, Pagination, Button, Toast } from "antd-mobile";
import { connect } from "react-redux"; // Expects that Highcharts was loaded in the code.
// const QRCode = require("qrcode.react");
// var echarts = require("echarts");
import { setValue } from "@/redux/keke-train";

// 引入 ECharts 主模块
const echarts = require("echarts/lib/echarts");
// 引入柱状图
require("echarts/lib/chart/pie");
// 引入提示框和标题组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/legend");
require("echarts/lib/component/title");
import { get } from "util/fetch";

import "./index.less";
const colorTheme = {
  0: "#e84118",
  1: "#00a8ff",
  2: "#fbc531",
  3: "#4cd137",
  4: "#487eb0",
  5: "#9c88ff"
};
let randomCount = parseInt(Math.random() * 10) % 5;

class index extends Component {
  state = {
    total: 0,
    option: {},
    correctCount: 0,
    errorCount: 0,
    chartData: []
  };
  componentDidMount() {
    let { kekeTrain } = this.props;
    let { wordList, tipCountList } = kekeTrain;
    // let
    let counts = {};

    tipCountList.forEach(function(x) {
      counts[x] = (counts[x] || 0) + 1;
    });
    let chartData = Object.keys(counts).map(key => {
      let item = {
        value: counts[key],
        name: `提示${key}次`
      };
      return item;
    });
    this.setState({ chartData });
    let chartLegend = Object.keys(counts).map(key => `提示${key}次`);
    log(chartData, chartLegend);
    let option = {
      tooltip: {
        trigger: "item",
        formatter: "{a} <br/>{b} : {c} ({d}%)"
      },
      title: {
        text: "提示使用统计",
        x: "center"
      },
      legend: {
        // orient: "vertical",
        // x: "left",
        // bottom: 40,
        y: "bottom",
        data: chartLegend
      },
      grid: { bottom: 120, top: 20, left: 80 },

      color: ["#4cd137", "#00a8ff", "#fbc531", "#e84118", "#487eb0", "#9c88ff"],
      // calculable: true,
      series: {
        name: "难度",
        type: "pie",
        radius: "45%",
        center: ["50%", "45%"],
        data: chartData
      }
    };
    let myChart = echarts.init(document.getElementById("chart"));
    myChart.setOption(option);
  }
  gotoLast = () => {
    this.props.history.go(-1);
  };
  goToNextCollection = () => {
    let pageNo = this.props.kekeTrain.collection.current + 1;
    let total = this.props.kekeTrain.collection.total;
    let params = {
      pageSize: 1,
      pageNo
    };
    if (pageNo > total) {
      Toast.info("最后的词库", 1, () => {
        this.props.history.push(`/keke-collection`);
      });
      return;
    }
    get(`/api/english/keke-collection`, params).then(res => {
      let value = {
        collection: {
          total: res.count,
          current: this.props.kekeTrain.collection.current + 1
        }
      };
      this.props.setValue(value);
      this.props.history.push(
        `/keke-one-collection?collection_id=${res.data[0].id}`
      );
      log(res);
    });
  };
  render() {
    let { kekeTrain } = this.props;
    log(kekeTrain);
    const navBar = (
      <NavBar
        style={{
          backgroundColor: colorTheme[randomCount],
          color: "#fff"
        }}
        mode="light"
        onLeftClick={this.gotoLast}
        leftContent={<Icon type="left" />}
        rightContent={[<Icon key="1" type="ellipsis" />]}
      >
        Review and learn
      </NavBar>
    );
    const title = (
      <div className="title">
        <h2>词汇总结</h2>
        <div className="exam">
          {kekeTrain && kekeTrain.collection_info.name}{" "}
          <span className="tip-render">
            [{kekeTrain.collection.current}/{kekeTrain.collection.total}]
          </span>
          <p>
            {kekeTrain && kekeTrain.collection_info.note}{" "}
            <span>[{kekeTrain && kekeTrain.wordList.length}词汇]</span>
          </p>
        </div>
      </div>
    );
    const correctCell = (
      <div className="correct">
        用时
        <span>{this.props.kekeTrain.duration}</span>秒{"   "}
        {this.state.chartData.map(item => (
          <div key={item.name} style={{ display: "inline" }}>
            <span>{item.value}</span>个单词
            {item.name}，
          </div>
        ))}
      </div>
    );
    const chart = (
      <div id="chart" className="chart">
        chart
      </div>
    );

    // const qrCode = (
    //   <div className="qrcode">
    //     <QRCode
    //       value={kekeTrain.exam_address}
    //       fgColor={colorTheme[randomCount]}
    //       bgColor="transparent"
    //       size={100}
    //     />
    //     <p>题库地址</p>
    //   </div>
    // );
    const content = (
      <div className="content">
        {title}
        {correctCell}
        {chart}

        {/* {qrCode} */}
        <Button className="button" onClick={this.goToNextCollection}>
          下一词库
        </Button>
      </div>
    );
    return (
      <div className="keke-chart">
        {navBar}
        {content}
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};
const mapStateToProps = state => ({
  kekeTrain: state.kekeTrain
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
